<body><div class="pizza is-hidden-mobile"><a @click.prevent="showLightning = true"><img src="pizza.png"><div><span>If you like it</span><br><span>buy me a pizza :)</span></div></a></div><!-- Modal f√ºr Lightning QR--><div class="lightning-modal-overlay" v-if="showLightning" @click.self="showLightning = false"><div class="lightning-modal"><button class="lightning-close" @click="showLightning = false" aria-label="Close">√ó</button><img class="qr" src="tip_lightning_code.svg" alt="Lightning QR Code"><h3>Oh, what kind of pizza is this? A lightning-fast one! ‚ö°Ô∏èüçï</h3><p>This QR code is a Lightning address. Open your wallet, scan it, and send a few sats ‚Äî like a tip,
only faster than the oven preheats.</p><p>Lightning is Bitcoin's turbo change: cheap, fast, and hassle-free.</p><p>New to Lightning? A quick five-minute dive might be the most valuable use of your time today.</p><p>Got ~44 minutes? <a href="https://www.youtube.com/watch?v=Pef22g53zsg" target="_blank">Here is a great introduction by Jack Mallers</a>.
</p></div></div><div class="container"><div class="section"><h1 class="title marginless">zsh 4 humans<a href="https://github.com/kakulukia/dotfiles-public" title="Go to the GitHub-Repo"><img class="github" src="github.png"></a></h1><a class="claim" href="https://ohmyz.sh/" target="_blank">Your terminal never felt <i>this</i> good before.‚Ñ¢</a></div><div class="section"><asciinema-player class="is-hidden-mobile" id="player" v-pre src="demo.cast" poster="npt:0:01" speed="1.5" idle-time-limit="1"></asciinema-player><iframe class="is-hidden-tablet" width="560" height="315" src="https://www.youtube.com/embed/nkT3tQFLddU?VQ=HD720" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe><h2 class="subtitle">Intro</h2><p>For the last several years (initial commit 2014-10-04) I have been building up my shell environment and it's the
first thing that gets installed on a new <del>server or</del> Mac. Especially the reverse
history search is a huge time saver.</p><p>It's been a long journey from default bash to bash-it, oh-my-bash, then oh-my-zsh, to prezto, and‚Ä¶</p><p><strong>z4h</strong></p><p>‚Ä¶and now, apart from some tool that gets added
every now and then ‚Äî I finally feel like it can't get any better. üòé</p><p>Some of the stuff in this repo I didn't even know was possible before I switched to ZSH or
discovered app X. So I just wanted to share the whole collection. I did a few improvements myself,
but basically it was all out there. This repo enables everybody to get all the goodies at
once and helps me feel at home whenever I open a new shell.
</p><h2 class="subtitle">Installation</h2><p>Yes, the installation process is a bit more tricky than the last version, BUT this only needs to be done <strong>1</strong> time.
Z4h will <a href="https://asciinema.org/a/542763">teleport</a> your shell to whichever system you are logging in to. So over there you only may need to install some tools. That's it!
</p><p><br><strong>Preparing your environment:</strong></p><ol><li>Fork this dotfiles-public repo (change <code>.gitconfig</code> to match your username and email)</li><li>Create a dotfile-private repo and let GitHub create a ReadMe to have some content to download</li><li>Copy your private key to the new machine or create a new one and add it to GitHub</li><li>Make sure this key is loaded by ssh if its name differs from id_rsa</li><li>Make sure <strong>git, zsh, python, curl</strong> and the <strong>build-essential package</strong> (for installing extra tools) is installed</li><li>Use the following command to bootstrap your Mac (only tested on macOS so far; should also work on Linux though)</li><li>Make sure to change <code>GITHUB_USERNAME</code> to your GitHub username</li></ol><br><blockquote>GITHUB_USERNAME=kakulukia bash -c 'curl -fsSL "https://raw.githubusercontent.com/$GITHUB_USERNAME/dotfiles-public/main/bin/bootstrap-machine.sh" | bash'

</blockquote><br><p><strong>This will do the following:</strong></p><ul><li>It downloads the previously created public and private repos directly to your home folder and adds .dotfiles-public/.dotfiles-private as the git dirs for those repos</li><li>The next time you start your terminal it will automatically install z4h</li><li>You can cycle between public, private and no repo with Ctrl+P<figure><img src="switch_repos.png" title="switching repos"><figcaption>cycle between the repos with Ctrl+P</figcaption></figure></li></ul><p>Now you are free to add any file under <code>$HOME</code> to your public or private settings and keep them.</p><p>In the misc folder you can find:</p><ul><li>The Powerline patched SourceCode font (you may use any <a href="https://www.nerdfonts.com/" target="_blank">Nerdfont</a>)</li><li>better-osx-settings script</li><li>An opinionated set of <a href="https://github.com/kakulukia/dotfiles-public/blob/main/bin/apps.json">tools</a> that can be
installed via <span class="command">select-tools</span>
</li></ul><p>Your settings (public and private) can be updated via <span class="command">sync-dotfiles</span></p><h2 class="subtitle">Features</h2><p>Apart from what's shown in the recording, here is some more of what's included:</p><ul><li>suggestions (grey text) - use the right arrow key to accept</li><li>navigate with Shift+Arrow keys<ul><li>Shift+up ‚Üí dir up</li><li>Shift+down ‚Üí search any visited path and jump there</li><li>Shift+Left/Right ‚Üí cycle through your persistent path history</li></ul></li><li><span>history reverse search - use the up arrow to cycle through previous commands.
Anything you typed before hitting the up key will be used as a filter and be highlighted.
This seriously saves a lot of typing. üòá</span><br><br><figure><img src="reverse_search.png" title="context sensitive history search"><figcaption>mm + up arrow</figcaption></figure><p><strong>Pro tip:</strong> add <code># whatever tag</code> to your command. Next time when you decide to run it, type your tag or a part of it, press up and enter. This is how you can "tag" commands and easily find them later. You can apply more than one "tag". Technically, everything after # is a comment. (thx romkatv!)
</p></li><li>red / green colored commands - shows that the command is (un)available</li><li>. is in the path so no need for any ./ prefixing executables</li><li>path<ul><li>print a sorted version of $PATH</li><li>add the given folder to $PATH and</li><li>append the given path to your .zsh-profile when called with <span class="command">path --save DIR</span></li></ul></li><li>mv with one argument - no need to type the file name twice for renaming</li><li>,, - jump to the git root dir</li><li>o - will open the finder in the current directory</li><li><a href="https://github.com/kakulukia/dotfiles-public/tree/main/bin">cdto.app</a> - there is an app in the bin folder that provides a
way to reverse the above trick and opens a terminal at the current Finder location.
Use the Command key to just drag it into the Finder's toolbar.<br><br><figure><img src="cdto.png" title="cdto in the finder"><figcaption>The result will look like this</figcaption></figure></li><li>Just for self-reference the app next to cdto is <a href="https://github.com/Ji4n1ng/OpenInTerminal">OpenInTerminal</a></li><li><a href="https://github.com/chamburr/glance">QuickLook Plugins</a><span> for macOS</span></li><li>diff - aliased to diff-so-fancy in general, not just the git version</li><li>ips - will show all local ips (IPV4) / ip[4|6] will show some info about your external one</li><li>ping - is aliased to prettyping</li><li>rg - alias for "rg -S --max-columns 444" won't clutter the screen with nasty one line files</li><li>I love the global alias G for "| rg" ‚Äî I use that a lot</li><li>A customized starship.toml for a minimal prompt</li><li>up - that's the live preview pipe thing you saw at the end of the screencast. It's activated with CTRL+P for pipe.</li><li>The rest can be found in the <a href="https://github.com/kakulukia/dotfiles-public/tree/main/.alias">.alias</a> config</li><li>For everyone who forgets a leading sudo from time to time - you can now answer that error with a simple <span class="command">please</span> (just do it) :)</li><li><a href="https://github.com/LazyVim/LazyVim">LazyVim</a> is making editing in the terminal a pleasure</li><li>And finally the startup speed is very good:<figure><img src="benchmark.png" title="startup benchmark"><figcaption>current startup speed with all features enabled</figcaption></figure></li></ul><h2 class="subtitle">ToDo</h2><ul><li>This list is finally empty :)</li></ul><h2 class="subtitle">Credits</h2><p>This theme was inspired by<a href="https://github.com/Bash-it/bash-it/blob/master/themes/powerline-plain/powerline-plain.theme.bash" target="_blank"> PowerlinePlain</a> and based on<a href="https://github.com/caiogondim/bullet-train-oh-my-zsh-theme" target="_blank"> BulletTrain</a>.<br><p>The awesome z4h by <a href="https://github.com/romkatv/zsh4humans">romkatv</a>.</p><strong>Generally:</strong> Mad props to all awesome devs who build most of the apps referenced here.
Too many to list em all, but most if not all do feature a credit line inside the scripts or a reference to their repos.</p></div></div></body>